"use client"

import { DashboardNav } from "@/components/dashboard-nav"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { TimePicker } from "@/components/ui/time-picker"
import { Clock, Truck, CheckCircle2, MapPin, Package, Eye } from "lucide-react"
import { TransportMap } from "@/components/transport-map"
import { withAuth } from "@/components/with-auth"
import { useState, useEffect } from "react"
import { toast } from "sonner"

type TransportItem = {
  id: string
  pickupRequestId: string
  transportType: string
  provider?: string
  assignedTo?: string
  pickupDate: string
  pickupWindow: {
    from: string
    to: string
  }
  cost?: number
  trackingLink?: string
  notes?: string
  status: string
  pickupRequest?: {
    itemName: string
    quantity: number
    requesterName: string
  }
  assignedToName?: string
}

type PickupRequestOption = {
  id: string
  itemName: string
  quantity: number
  requesterName: string
  requesterType: string
  status: string
}

function TransportationPage() {
  const [transports, setTransports] = useState<TransportItem[]>([])
  const [availablePickups, setAvailablePickups] = useState<PickupRequestOption[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [isDialogOpen, setIsDialogOpen] = useState(false)
  const [formData, setFormData] = useState({
    pickupRequestId: "",
    pickupMethod: "staff" as "staff" | "courier",
    assignedTo: "",
    pickupDate: "",
    pickupWindowFrom: "",
    pickupWindowTo: "",
    notes: "",
  })

  const mockTransports = [
    {
      id: "T-014",
      cafe: "Green Café",
      destination: "EcoFarm",
      type: "Coffee Grounds",
      quantity: 15.2,
      status: "Scheduled",
      date: "2025-01-10",
      time: "10:00 AM",
    },
    {
      id: "T-015",
      cafe: "BrewHub",
      destination: "FoodBank NGO",
      type: "Food Waste",
      quantity: 22.0,
      status: "In Transit",
      date: "2025-01-09",
      time: "2:30 PM",
    },
    {
      id: "T-016",
      cafe: "Green Café",
      destination: "Urban Farm Co.",
      type: "Organic Waste",
      quantity: 18.5,
      status: "Completed",
      date: "2025-01-08",
      time: "9:00 AM",
    },
    {
      id: "T-017",
      cafe: "Coffee Corner",
      destination: "Community Garden",
      type: "Coffee Grounds",
      quantity: 12.3,
      status: "Scheduled",
      date: "2025-01-11",
      time: "11:30 AM",
    },
    {
      id: "T-018",
      cafe: "Daily Brew",
      destination: "Local Farm",
      type: "Food Waste",
      quantity: 25.8,
      status: "In Transit",
      date: "2025-01-09",
      time: "3:45 PM",
    },
    {
      id: "T-019",
      cafe: "Green Café",
      destination: "EcoFarm",
      type: "Plate Waste",
      quantity: 14.2,
      status: "Completed",
      date: "2025-01-07",
      time: "1:00 PM",
    },
  ]

  // Use fetched transports or fallback to mock data
  const displayTransports = transports.length > 0
    ? transports
    : mockTransports.map((t) => ({
        id: t.id,
        pickupRequestId: "",
        transportType: "internal",
        pickupDate: t.date,
        pickupWindow: { from: t.time, to: t.time },
        status: t.status,
        pickupRequest: {
          itemName: t.type,
          quantity: t.quantity,
          requesterName: t.destination,
        },
      }))

  const scheduledCount = displayTransports.filter(
    (t) => {
      const status = t.status.toLowerCase()
      return status === "scheduled" || status === "pickup-scheduled"
    }
  ).length
  const inTransitCount = displayTransports.filter(
    (t) => t.status === "in-transit" || t.status === "In Transit"
  ).length
  const completedCount = displayTransports.filter(
    (t) => t.status === "completed" || t.status === "Completed"
  ).length

  const getStatusColor = (status: string) => {
    const normalized = status.toLowerCase()
    if (normalized === "scheduled" || normalized === "pickup-scheduled") {
      return "bg-yellow-500/10 text-yellow-600 border-yellow-500/20"
    }
    if (normalized === "in-transit" || normalized === "in transit") {
      return "bg-blue-500/10 text-blue-600 border-blue-500/20"
    }
    if (normalized === "completed") {
      return "bg-primary/10 text-primary border-primary/20"
    }
    return "bg-muted text-muted-foreground"
  }

  const getStatusIcon = (status: string) => {
    const normalized = status.toLowerCase()
    if (normalized === "scheduled" || normalized === "pickup-scheduled") {
      return <Clock className="w-4 h-4" />
    }
    if (normalized === "in-transit" || normalized === "in transit") {
      return <Truck className="w-4 h-4" />
    }
    if (normalized === "completed") {
      return <CheckCircle2 className="w-4 h-4" />
    }
    return null
  }

  // Fetch transports and available pickup requests
  useEffect(() => {
    fetchTransports()
    fetchAvailablePickups()
  }, [])

  const fetchTransports = async () => {
    try {
      const storedUser = JSON.parse(localStorage.getItem("user") || "{}")
      if (!storedUser.userId) return

      const response = await fetch("/api/transports", {
        headers: { "x-user-id": storedUser.userId },
      })

      if (response.ok) {
        const data = await response.json()
        setTransports(data)
      }
    } catch (error) {
      console.error("Error fetching transports:", error)
    } finally {
      setIsLoading(false)
    }
  }

  const fetchAvailablePickups = async () => {
    try {
      const storedUser = JSON.parse(localStorage.getItem("user") || "{}")
      if (!storedUser.userId) return

      const response = await fetch("/api/admin/pickups", {
        headers: { "x-user-id": storedUser.userId },
      })

      if (response.ok) {
        const data = await response.json()
        // Filter for approved requests without transport
        const available = data.filter(
          (req: PickupRequestOption) => req.status === "approved" || req.status === "scheduled"
        )
        setAvailablePickups(available)
      }
    } catch (error) {
      console.error("Error fetching pickup requests:", error)
    }
  }

  const handleSchedulePickup = async () => {
    if (!formData.pickupRequestId || !formData.pickupDate || !formData.pickupWindowFrom || !formData.pickupWindowTo || !formData.assignedTo) {
      toast.error("Please fill in all required fields")
      return
    }

    try {
      const storedUser = JSON.parse(localStorage.getItem("user") || "{}")
      if (!storedUser.userId) {
        toast.error("User not found")
        return
      }

      const response = await fetch("/api/transports", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "x-user-id": storedUser.userId,
        },
        body: JSON.stringify({
          pickupRequestId: formData.pickupRequestId,
          transportType: formData.pickupMethod === "staff" ? "internal" : "external",
          provider: formData.pickupMethod === "courier" ? "External Courier" : undefined,
          assignedTo: formData.assignedTo,
          pickupDate: formData.pickupDate,
          pickupWindow: {
            from: formData.pickupWindowFrom,
            to: formData.pickupWindowTo,
          },
          notes: formData.notes || undefined,
        }),
      })

      if (response.ok) {
        toast.success("Pickup scheduled successfully")
        setIsDialogOpen(false)
        // Reset form
        setFormData({
          pickupRequestId: "",
          pickupMethod: "staff",
          assignedTo: "",
          pickupDate: "",
          pickupWindowFrom: "",
          pickupWindowTo: "",
          notes: "",
        })
        // Refresh data without page reload
        fetchTransports()
        fetchAvailablePickups()
      } else {
        const errorData = await response.json()
        toast.error(errorData.error || "Failed to schedule pickup")
      }
    } catch (error) {
      console.error("Error scheduling pickup:", error)
      toast.error("Something went wrong. Please try again.")
    }
  }

  const handleMarkCompleted = async (transportId: string, pickupRequestId: string) => {
    try {
      const storedUser = JSON.parse(localStorage.getItem("user") || "{}")
      if (!storedUser.userId) {
        toast.error("User not found")
        return
      }

      const response = await fetch(`/api/transports/${transportId}/complete`, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
          "x-user-id": storedUser.userId,
        },
        body: JSON.stringify({
          pickupRequestId,
        }),
      })

      if (response.ok) {
        toast.success("Pickup marked as completed")
        // Refresh data without page reload
        fetchTransports()
        fetchAvailablePickups()
      } else {
        const errorData = await response.json()
        toast.error(errorData.error || "Failed to mark as completed")
      }
    } catch (error) {
      console.error("Error marking as completed:", error)
      toast.error("Something went wrong. Please try again.")
    }
  }

  return (
    <div className="min-h-screen bg-background">
      <DashboardNav />

      <main className="pt-20 px-4 pb-8">
        <div className="container mx-auto max-w-7xl">
          {/* Header */}
          <div className="mb-6 flex items-start gap-3">
            <div className="w-12 h-12 rounded-xl bg-primary/10 flex items-center justify-center flex-shrink-0">
              <Truck className="w-6 h-6 text-primary" />
            </div>
            <div>
              <h1 className="font-display font-bold text-3xl text-foreground mb-1">Transportation Organizer</h1>
              <p className="text-muted-foreground">Manage food waste pickups and deliveries</p>
            </div>
          </div>

          {/* Overview Cards */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
            <Card className="p-6 hover:shadow-lg transition-shadow bg-gradient-to-br from-yellow-50 to-background border-2 border-yellow-500/20">
              <div className="flex items-center justify-between mb-4">
                <div className="w-12 h-12 rounded-xl bg-yellow-500/10 flex items-center justify-center">
                  <Clock className="w-6 h-6 text-yellow-600" />
                </div>
              </div>
              <div className="text-3xl font-display font-bold text-foreground mb-1">{scheduledCount}</div>
              <div className="text-sm text-muted-foreground font-medium">Scheduled Pickups</div>
              <div className="text-xs text-muted-foreground mt-1">Upcoming waste collections</div>
            </Card>

            <Card className="p-6 hover:shadow-lg transition-shadow bg-gradient-to-br from-blue-50 to-background border-2 border-blue-500/20">
              <div className="flex items-center justify-between mb-4">
                <div className="w-12 h-12 rounded-xl bg-blue-500/10 flex items-center justify-center">
                  <Truck className="w-6 h-6 text-blue-600" />
                </div>
              </div>
              <div className="text-3xl font-display font-bold text-foreground mb-1">{inTransitCount}</div>
              <div className="text-sm text-muted-foreground font-medium">In Transit</div>
              <div className="text-xs text-muted-foreground mt-1">Active deliveries in progress</div>
            </Card>

            <Card className="p-6 hover:shadow-lg transition-shadow bg-gradient-to-br from-green-50 to-background border-2 border-primary/20">
              <div className="flex items-center justify-between mb-4">
                <div className="w-12 h-12 rounded-xl bg-primary/10 flex items-center justify-center">
                  <CheckCircle2 className="w-6 h-6 text-primary" />
                </div>
              </div>
              <div className="text-3xl font-display font-bold text-foreground mb-1">{completedCount}</div>
              <div className="text-sm text-muted-foreground font-medium">Completed Deliveries</div>
              <div className="text-xs text-muted-foreground mt-1">Total finished transports</div>
            </Card>
          </div>

          {/* Action Buttons */}
          <div className="flex flex-wrap gap-3 mb-6">
            <Button
              className="bg-primary hover:bg-primary/90 text-primary-foreground"
              onClick={() => setIsDialogOpen(true)}
            >
              <Truck className="w-4 h-4 mr-2" />
              Schedule Pickup
            </Button>
            <Button variant="outline" className="border-primary/20 hover:bg-primary/5 bg-transparent">
              Update Status
            </Button>
          </div>

          <div className="grid lg:grid-cols-3 gap-6 mb-8">
            {/* Transport Table */}
            <Card className="p-6 bg-card lg:col-span-2">
              <div className="flex items-center justify-between mb-6">
                <h3 className="font-display font-bold text-xl text-foreground">Transport Requests</h3>
                <Button variant="outline" size="sm">
                  Export
                </Button>
              </div>

              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead>
                    <tr className="border-b border-border bg-muted/50">
                      <th className="text-left py-3 px-4 font-display font-semibold text-sm text-muted-foreground">
                        ID
                      </th>
                      <th className="text-left py-3 px-4 font-display font-semibold text-sm text-muted-foreground">
                        Café
                      </th>
                      <th className="text-left py-3 px-4 font-display font-semibold text-sm text-muted-foreground">
                        Destination
                      </th>
                      <th className="text-left py-3 px-4 font-display font-semibold text-sm text-muted-foreground">
                        Type
                      </th>
                      <th className="text-left py-3 px-4 font-display font-semibold text-sm text-muted-foreground">
                        Quantity
                      </th>
                      <th className="text-left py-3 px-4 font-display font-semibold text-sm text-muted-foreground">
                        Status
                      </th>
                      <th className="text-left py-3 px-4 font-display font-semibold text-sm text-muted-foreground">
                        Action
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    {displayTransports.map((transport) => {
                      const destination = transport.pickupRequest?.requesterName || "Unknown"
                      const itemType = transport.pickupRequest?.itemName || "Unknown"
                      const quantity = transport.pickupRequest?.quantity || 0
                      const cafe = "Green Café" // Could be fetched from waste data

                      return (
                        <tr key={transport.id} className="border-b border-border hover:bg-accent/5 transition-colors">
                          <td className="py-4 px-4 font-medium text-foreground">{transport.id}</td>
                          <td className="py-4 px-4">
                            <div className="flex items-center gap-2">
                              <div className="w-8 h-8 rounded-full bg-primary/10 flex items-center justify-center">
                                <Package className="w-4 h-4 text-primary" />
                              </div>
                              <span className="text-foreground">{cafe}</span>
                            </div>
                          </td>
                          <td className="py-4 px-4">
                            <div className="flex items-center gap-2">
                              <MapPin className="w-4 h-4 text-muted-foreground" />
                              <span className="text-foreground">{destination}</span>
                            </div>
                          </td>
                          <td className="py-4 px-4 text-muted-foreground">{itemType}</td>
                          <td className="py-4 px-4 font-medium text-foreground">{quantity} kg</td>
                          <td className="py-4 px-4">
                            <Badge variant="outline" className={getStatusColor(transport.status)}>
                              <span className="flex items-center gap-1.5">
                                {getStatusIcon(transport.status)}
                                {transport.status}
                              </span>
                            </Badge>
                          </td>
                          <td className="py-4 px-4">
                            {(transport.status === "pickup-scheduled" || transport.status === "scheduled" || transport.status === "Scheduled") ? (
                              <Button
                                variant="outline"
                                size="sm"
                                onClick={() => handleMarkCompleted(transport.id, transport.pickupRequestId)}
                                className="hover:bg-green-500/10 hover:text-green-600"
                              >
                                <CheckCircle2 className="w-4 h-4 mr-1" />
                                Mark as Completed
                              </Button>
                            ) : (
                              <Button variant="ghost" size="sm" className="hover:bg-primary/10 hover:text-primary">
                                <Eye className="w-4 h-4 mr-1" />
                                View
                              </Button>
                            )}
                          </td>
                        </tr>
                      )
                    })}
                  </tbody>
                </table>
              </div>
            </Card>

            {/* Transport Map */}
            <Card className="p-6 bg-card">
              <h3 className="font-display font-bold text-xl text-foreground mb-6">Route Tracking</h3>
              <TransportMap />
              <div className="mt-6 space-y-3">
                <div className="flex items-center justify-between p-3 rounded-lg bg-muted/50">
                  <div className="flex items-center gap-2">
                    <div className="w-2 h-2 rounded-full bg-primary animate-pulse" />
                    <span className="text-sm font-medium text-foreground">Active Routes</span>
                  </div>
                  <span className="text-sm font-bold text-primary">{inTransitCount}</span>
                </div>
                <div className="flex items-center justify-between p-3 rounded-lg bg-muted/50">
                  <div className="flex items-center gap-2">
                    <div className="w-2 h-2 rounded-full bg-yellow-500" />
                    <span className="text-sm font-medium text-foreground">Scheduled</span>
                  </div>
                  <span className="text-sm font-bold text-yellow-600">{scheduledCount}</span>
                </div>
                <div className="flex items-center justify-between p-3 rounded-lg bg-muted/50">
                  <div className="flex items-center gap-2">
                    <div className="w-2 h-2 rounded-full bg-muted-foreground" />
                    <span className="text-sm font-medium text-foreground">Completed Today</span>
                  </div>
                  <span className="text-sm font-bold text-muted-foreground">3</span>
                </div>
              </div>
            </Card>
          </div>

          {/* Additional Stats */}
          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6">
            <Card className="p-6 bg-card border-2 border-border hover:border-primary/20 transition-colors">
              <div className="text-2xl font-display font-bold text-foreground mb-1">48.5 km</div>
              <div className="text-sm text-muted-foreground">Total Distance Today</div>
            </Card>
            <Card className="p-6 bg-card border-2 border-border hover:border-primary/20 transition-colors">
              <div className="text-2xl font-display font-bold text-foreground mb-1">156.2 kg</div>
              <div className="text-sm text-muted-foreground">Total Waste Transported</div>
            </Card>
            <Card className="p-6 bg-card border-2 border-border hover:border-primary/20 transition-colors">
              <div className="text-2xl font-display font-bold text-foreground mb-1">8</div>
              <div className="text-sm text-muted-foreground">Active Partners</div>
            </Card>
            <Card className="p-6 bg-card border-2 border-border hover:border-primary/20 transition-colors">
              <div className="text-2xl font-display font-bold text-foreground mb-1">98%</div>
              <div className="text-sm text-muted-foreground">On-Time Delivery Rate</div>
            </Card>
          </div>
        </div>
      </main>

      {/* Schedule Pickup Dialog */}
      <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
        <DialogContent className="sm:max-w-[600px] max-h-[90vh] overflow-y-auto">
          <DialogHeader>
            <DialogTitle>Schedule Pickup</DialogTitle>
            <DialogDescription>
              Schedule a pickup for an approved request
            </DialogDescription>
          </DialogHeader>
          <div className="space-y-4 py-4">
            <div className="space-y-2">
              <Label htmlFor="pickupRequest">Pickup Request *</Label>
              <Select
                value={formData.pickupRequestId}
                onValueChange={(value) => setFormData({ ...formData, pickupRequestId: value })}
              >
                <SelectTrigger id="pickupRequest">
                  <SelectValue placeholder="Select a pickup request" />
                </SelectTrigger>
                <SelectContent>
                  {availablePickups.length === 0 ? (
                    <SelectItem value="none" disabled>No available pickup requests</SelectItem>
                  ) : (
                    availablePickups.map((pickup) => (
                      <SelectItem key={pickup.id} value={pickup.id}>
                        {pickup.itemName} - {pickup.requesterName} ({pickup.quantity} kg)
                      </SelectItem>
                    ))
                  )}
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <Label htmlFor="pickupMethod">Pickup Method *</Label>
              <Select
                value={formData.pickupMethod}
                onValueChange={(value: "staff" | "courier") =>
                  setFormData({ ...formData, pickupMethod: value })
                }
              >
                <SelectTrigger id="pickupMethod">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="staff">Café staff</SelectItem>
                  <SelectItem value="courier">External courier (Grab / Lalamove)</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <Label htmlFor="assignedTo">Staff or Courier name *</Label>
              <Input
                id="assignedTo"
                placeholder={formData.pickupMethod === "staff" ? "Staff member name" : "Courier name"}
                value={formData.assignedTo}
                onChange={(e) => setFormData({ ...formData, assignedTo: e.target.value })}
                required
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="pickupDate">Pickup Date *</Label>
              <Input
                id="pickupDate"
                type="date"
                value={formData.pickupDate}
                onChange={(e) => setFormData({ ...formData, pickupDate: e.target.value })}
                min={new Date().toISOString().split("T")[0]}
                required
              />
            </div>

            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="pickupWindowFrom">Pickup Time From *</Label>
                <Input
                  id="pickupWindowFrom"
                  type="time"
                  value={formData.pickupWindowFrom}
                  onChange={(e) => setFormData({ ...formData, pickupWindowFrom: e.target.value })}
                  required
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="pickupWindowTo">Pickup Time To *</Label>
                <Input
                  id="pickupWindowTo"
                  type="time"
                  value={formData.pickupWindowTo}
                  onChange={(e) => setFormData({ ...formData, pickupWindowTo: e.target.value })}
                  required
                />
              </div>
            </div>

            <div className="space-y-2">
              <Label htmlFor="notes">Optional notes</Label>
              <Textarea
                id="notes"
                placeholder="Additional notes or instructions..."
                value={formData.notes}
                onChange={(e) => setFormData({ ...formData, notes: e.target.value })}
                rows={3}
              />
            </div>
          </div>
          <DialogFooter>
            <Button variant="outline" onClick={() => setIsDialogOpen(false)}>
              Cancel
            </Button>
            <Button onClick={handleSchedulePickup} className="bg-primary hover:bg-primary/90">
              <Truck className="w-4 h-4 mr-2" />
              Schedule Pickup
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}

export default withAuth(TransportationPage, ["staff"])
